.items-container {
    padding: 50px 0px;
}

.home-container {
    /* background-color: black; */
    background-image: url('../../img/headerimg.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 150px;
    position: relative;
    top: 0px;
    left: 0px;
    right: 0px;
    z-index: -10;
    padding-left: 48px;
    padding-right: 48px;
    padding-bottom: 100px;
}

.home-container h1 {
    font-size: 40px;
    font-weight: 500;
    color: #fff;
}

.home-container p {
    font-size: 16px;
    color: #fff;
}

.btn {
    background-color: #fff;
    color: #0A0661;
    font-size: 15px;
    font-weight: 600;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    margin-top: 10px;
    display: inline-block;
}

.btn-3 {
    border: 2px solid #fff;
    background: transparent;
    color: #fff;
    border-radius: 0px;
}

@media screen and (max-width:769px) {
    .home-container {
        padding-top: 100px;
        padding-left: 35px;
        padding-right: 35px;
        padding-bottom: 50px;
    }

    .home-container::before {
        content: "";
        width: 100%;
        height: 100%;
        background-color: rgb(0 0 0 / 41%);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        /* bottom: 0; */
        z-index: -11;
    }
}

/* Grid home-items */
.home-items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    margin: 0px 48px;
    grid-template-columns: repeat(4, minmax(300px, 1fr));
}

.item {
    height: 310px;
    position: relative;
}
.item h3{
    font-family: 'Inter', sans-serif;
    font-size: 14px;
}
.item h3, .item p {
    color: #0A0661;
    font-weight: 600;
}

.item:nth-child(9) {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 3;
    grid-row-end: 5;
    height: 658px;
    padding: 0px;
    background-color: transparent;
    border-top: 8px solid #F9D695;
    border-bottom: 8px solid #F9D695;
}

.item:nth-child(25) {
    grid-column-start: 3;
    grid-column-end: 5;
    grid-row-start: 7;
    grid-row-end: 9;
    height: 658px;
    padding: 0px;
    background-color: transparent;
    position: relative;
    border-top: 8px solid #F9D695;
    border-bottom: 8px solid #F9D695;
}

.item:nth-child(9) img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    top: 0;
    left: 0;


}

.item:nth-child(9) h3 {
    z-index: 30;
    position: absolute;
    bottom: 100px;
    left: 30px;
    color: #fff;
}

.item:nth-child(9) button {
    z-index: 30;
    position: absolute;
    bottom: 50px;
    left: 30px;
    background-color: orange;
}

/*  */
.item:nth-child(25) img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    top: 0;
    left: 0;
}

.item:nth-child(25) h3 {
    z-index: 30;
    position: absolute;
    bottom: 100px;
    left: 30px;
    color: #fff;
}

.item:nth-child(25) button {
    z-index: 30;
    position: absolute;
    bottom: 50px;
    left: 30px;
    background-color: orange;
}


.item {
    padding: 10px;
}

.item .heart-icon {
    display: block;
    text-align: right;
    color: #0A0661;
    font-size: 30px;
    cursor: pointer;
    margin-bottom: 0px;
}

.item img {
    cursor: pointer;
    width: 100%;
    height: 200px;
    object-fit: cover;
}

.item h3 {
    margin: 0px 0px;
}

.item p {
    font-weight: 600;
}

/* text-container */
.text-container {
    padding: 0px 48px 50px 48px;
}

.top-text {
    text-align: center;
}

.btn-2 {
    border: 2px solid #0A0661;
    color: #0A0661;
    width: 200px;
    height: 56px;
    font-size: 18px;
    text-align: center;
    margin-bottom: 15px;
    display: inline-block;
    font-weight: 600;
}

.text-container .text {
    text-align: center;
    color: #0A0661;
    margin-bottom: 20px;
    font-weight: 600;
}

.text-container .text-pera {
    text-align: start;
    color: #0A0661;
    font-weight: 600;
}

@media screen and (max-width:970px) {
    .home-items {
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
        row-gap: 20px;
        margin: 0px 20px;
    }

    .item {
        box-shadow: 0px 0px 3px gray;
    }


    .item:nth-child(9) {
        grid-column-start: 2;
        grid-column-end: 4;
        grid-row-start: 3;
        grid-row-end: 5;
        height: 675px;
        border-top: 0px solid #F9D695;
        border-bottom: 0px solid #F9D695;
        border-left: 8px solid #F9D695;
        border-right: 8px solid #F9D695;
    }

    .item img {
        cursor: pointer;
        width: 100%;
        height: 180px;
        object-fit: cover;
    }

    .item:nth-child(25) {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 10;
        grid-row-end: 12;
        height: 675px;
        border-top: 0px solid #F9D695;
        border-bottom: 0px solid #F9D695;
        border-left: 8px solid #F9D695;
        border-right: 8px solid #F9D695;
    }

}

@media screen and (max-width:769px) {
    .item:nth-child(9) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 5;
        grid-row-end: 6;
        height: 325px;
        border-top: 5px solid #F9D695;
        border-bottom: 5px solid #F9D695;
        border-left: 0px solid #F9D695;
        border-right: 0px solid #F9D695;
    }

    .item:nth-child(25) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 13;
        grid-row-end: 14;
        height: 325px;
        border-top: 5px solid #F9D695;
        border-bottom: 5px solid #F9D695;
        border-left: 0px solid #F9D695;
        border-right: 0px solid #F9D695;
    }
}


@media screen and (max-width:565px) {
    .item {
        height: 290px;
    }
}

@media screen and (max-width:330px) {
    .item {
        height: 310px;
    }
}