.footer{
    display: flex;
    padding: 50px 48px;
    justify-content: space-between;
    background-color: #0A0661;
    color: #c1c1c4;
    flex-wrap: wrap;
    /* align-items: center; */
}
.footer ul li{
    margin-bottom: 10px;
}
.footer ul li a{
    color: #c1c1c4;
}
.footer ul li a:hover{
color: #fff;
}
.footer .social {
    display: flex;
    justify-content: start;
    margin-top: 100px;
    flex-wrap: wrap;
}
.footer .social li{
    margin-right: 15px;
    list-style: none;
}

.footer .footer-item{
    width: 20%;
}

.footer .footer-item{
    padding: 10px;
}

@media screen and (max-width:920px) {
    .footer .footer-item{
        width: 30%;
    }
}

@media screen and (max-width:769px) {
    .footer .footer-item{
        width: 40%;
        padding: 0px;
    }
}

@media screen and (max-width:450px) {
    .footer .footer-item{
        width: 100%;
        padding: 0px;
    }
}

