header{
    background-color: rgb(0 0 0 / 41%);
    padding: 10px 48px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
}
header a{
    color: white;
}
.mob-nab{
    display: none;
}
.header-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

}
ul{
    padding-left: 0px;
}
.login-box,.regester{
    display: flex;
}
.regester{
    margin: 0px 25px;
}
.regester li:nth-child(1){
    margin-right: 10px;
    color: white;
}

.login-left li:nth-child(1){
    margin-right: 20px;
}
/* navbar */
.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 50px 0px 60px 0px;
}

.navbar .nav-box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 20%;
    flex-wrap: wrap;
}
.header-logo{
    width: 200px;
    height: 150px;
    position: absolute;
    top: 75px;
    left: 44%;
}
@media screen and (max-width:970px){
    .navbar .nav-box{
        width: 40%;
    }
    .header-logo{
        top: 60px;
        left: 39%;
    }
}
@media screen and (max-width:769px){
    header{
        background-color: rgb(0 0 0 / 80%);
        padding: 20px 0px 0px 0px;
    }
    .header-logo{
        display: none;
    }
    .mob-nab{
        display: block;
        position: absolute;
        top: 35px;
        left: 30px;
        z-index: 100;
    }
    .mob-nab span{
        font-size: 30px;
        color: #fff;
    }
    .mob-nab span .cross{
        font-size: 30px;
    }
    .header-top{
        display: flex;
        justify-content: center;
        flex-direction: column;
        flex-wrap: wrap;
    }
    .login-box{
        display: flex;
        flex-direction: column;
    }
    .regester li:nth-child(1){
        margin-right: 0px;
        color: white;
    }
    
    .login-left li:nth-child(1){
        margin-right: 0px;
        margin-bottom: 15px;
    }
    /* navbar */
    .navbar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
    }
    
    .navbar .nav-box{
        display: flex;
        width: 100%;
        flex-wrap: wrap;
        flex-direction: column;
    }
    .login-left,.login-right,.nav-left,.nav-right{
        display: flex;
        align-items: center;
        padding-left: 0px;
    }
    li{
        margin-bottom: 15px;
    }
    .nav-right li:nth-child(3){
        margin-bottom: 0px;
    }
    /* cross */
    .toggle-show{
        display: none;
    transition: all 0.5s ease;
    }
    span .menu{
        display: block;
    }
    .navbar{
        margin: 20px;
    }
}